export const getEmailError = (emailError: {
	empty: boolean;
	format: boolean;
	alreadyRegistered: boolean;
}) => {
	if (emailError.empty) return true;
	if (emailError.format) return true;
	if (emailError.alreadyRegistered) return true;
};

export const getEmailErrorText = (emailError: {
	empty: boolean;
	format: boolean;
	alreadyRegistered: boolean;
}) => {
	if (emailError.empty) return 'Vnligen fyll i fltet fr email';
	if (emailError.format) return 'Vnligen ange en korrekt email-adress';
	if (emailError.alreadyRegistered)
		return 'Denna email finns redan registrerad';
};

export const getPasswordError = (passwordError: {
	empty: boolean;
	tooShort: boolean;
	format: boolean;
	notMatching: boolean;
}) => {
	if (passwordError.empty) return true;
	if (passwordError.tooShort) return true;
	if (passwordError.format) return true;
	if (passwordError.notMatching) return true;
};

export const getPasswordErrorText = (passwordError: {
	empty: boolean;
	tooShort: boolean;
	format: boolean;
	notMatching: boolean;
}) => {
	if (passwordError.empty) return 'Vnligen fyll i fltet fr lsenord';
	if (passwordError.tooShort) return 'Lsenordet mste best av minst 6 tecken';
	if (passwordError.format)
		return 'Lsenordet mste best av minst en bokstav och en siffra';
	if (passwordError.notMatching) return 'Lsenorden matchar inte';
	return null;
};